export function formatDate(date) {
    let diff = new Date() - date; //   

    if (diff < 1000) { //  1 
        return ' ';
    }

    let sec = Math.floor(diff / 1000); //    

    if (sec < 60) {
        return sec + ' . ';
    }

    let min = Math.floor(diff / 60000); //    
    if (min < 60) {
        return min + ' . ';
    }

    //  
    //       ///
    let d = date;
    d = [
        '0' + d.getDate(),
        '0' + (d.getMonth() + 1),
        '' + d.getFullYear(),
        '  ',
        '0' + d.getHours(),
        '0' + d.getMinutes()

    ].map(component => component.slice(-2)); //   2    

    //    
    return d.slice(0, 3).join('.') + ' ' + d.slice(3).join(':');
}
